<template lang="pug">
.layout.pre-entry
    .layout__main
        .layout__title  

        .layout__content
            p                    . , .  , 64
            p           ,      .

            Row
                Col
                    Input(
                        style="z-index: 1"
                        type="select"
                        label=" "
                        v-model="form.serviceType"
                        :options="serviceTypeOptions"
                        optionLabel="label")
            Row(:style="{marginTop:isMobile?'0':'20px'}" gutter="1.5" unit="rem")
                Col
                    Input(label="" v-model="form.phone" type="phone")
                Col
                    Input(label=" " v-model="form.date" type="date" format="DD.MM.YYYY")
            Row(:style="{marginTop:isMobile?'0':'20px'}" gutter="1.5" unit="rem")
                Col
                    Input(label=" " v-model="form.time" type="time" format="HH:mm")
                Col
                    Button(full :style="{marginTop:isMobile?'16px':''}")  

            p(:style="{marginTop:isMobile?'16px':'2rem'}")         08.00  17.00
                br
                | :   
    LayoutSidebar
        router-link(to="#")  
        SidebarSupport
</template>

<script>
import {
    LayoutSidebar,
    Row,
    Col,
    Input,
    Button,
    SidebarSupport
} from '@/components'

export default {
    name: 'PreEntry',
    data () {
        return {
            form: {
                serviceType: '',
                phone: '',
                date: '',
                time: '',
            },
            serviceTypeOptions: [
                { label: '   ,   ', value: 1 },
                { label: '  ', value: 2 },
                { label: ' ', value: 3 },
            ]
        }
    },
    computed: {
        isMobile () {
            return screen.width < 760
        }
    },
    components: {
        LayoutSidebar,
        Row,
        Col,
        Input,
        Button,
        SidebarSupport
    }
}
</script>

<style lang="sass">
.pre-entry
</style>