<template lang="pug">
.services-form.form
    .layout__main
        .layout__title     

        .form__wizard(
            style="margin-top: 1rem"
            :style="wizardStyle"
            )
            .form__wizard-steps
                .form__wizard-step(
                    :class="{'form__wizard-step_active': step === 1, 'form__wizard-step_completed': step > 1}"
                    )
                    .form__wizard-step-number 1
                    .form__wizard-step-title   
                .form__wizard-step(
                    :class="{'form__wizard-step_active': step === 2, 'form__wizard-step_completed': step > 2}"
                    )
                    .form__wizard-step-number 2
                    .form__wizard-step-title  
                .form__wizard-step(
                    :class="{'form__wizard-step_active': step === 3, 'form__wizard-step_completed': step > 3}"
                    )
                    .form__wizard-step-number 3
                    .form__wizard-step-title  
                .form__wizard-step(
                    :class="{'form__wizard-step_active': step === 4, 'form__wizard-step_completed': step > 4}"
                    )
                    .form__wizard-step-number 4
                    .form__wizard-step-title !

        div(v-if="step === 1")
            .form__section
                .form__section-title  
                .form__section-content
                    Row(gutter="1.5" unit="rem")
                        Col
                            Input(label="" v-model="form.user.lastName")
                        Col
                            Input(label="" v-model="form.user.firstName")
                        Col
                            Input(label="" v-model="form.user.middleName")
                    Row(gutter="1.5" unit="rem" :style="{marginTop:isMobile?'0':'20px'}")
                        Col(:shrink="2")
                            Input(label=" " v-model="form.user.birthDay" type="date" format="DD.MM.YYYY")
                        Col(:shrink="1")
                            Input(label=" " v-model="form.user.birthPlace")
                    Row(gutter="1.5" unit="rem" :style="{marginTop:isMobile?'0':'20px'}")
                        Col
                            Input(label=" " v-model="form.user.residence")
                        Col
                            Input(label="" v-model="form.user.inn")
                    Row(gutter="1.5" unit="rem" :style="{marginTop:isMobile?'0':'20px'}")
                        Col
                            Input(label=". " v-model="form.user.email" type="email")
                        Col
                            Input(label=" " v-model="form.user.phone" type="phone")
                        Col
                            Input(label="  " v-model="form.user.additionalPhone" type="phone")
            .form__section
                .form__section-title  
                .form__section-content
                    Row(gutter="1.5" unit="rem")
                        Col(:shrink="2")
                            Input(label="" v-model="form.passport.series")
                        Col(:shrink="1")
                            Input(label="" v-model="form.passport.number")
                    Row(gutter="1.5" unit="rem" :style="{marginTop:isMobile?'0':'20px'}")
                        Col(:shrink="1")
                            Input(label=" " v-model="form.passport.organization")
                        Col(:shrink="2")
                            Input(label=" " v-model="form.passport.issueDate" type="date" format="DD.MM.YYYY")
                    Row(gutter="1.5" unit="rem" :style="{marginTop:isMobile?'0':'20px'}")
                        Col(:shrink="1")
                            Input(label=" " v-model="form.passport.registerPlace")
                        Col(:shrink="2")
            .form__section
                .form__section-title    
                .form__section-content
                    Checklist(
                        label="   "
                        :items="realtyTypeItems",
                        type="radio"
                        v-model="form.realty.type"
                        :cols="2"
                    )

                    Row(gutter="1.5" unit="rem" :style="{marginTop:isMobile?'0':'20px'}")
                        Col(:shrink="1")
                            Input(label=" " v-model="form.realty.address")
                        Col(:shrink="2")
                    Row(gutter="1.5" unit="rem" :style="{marginTop:isMobile?'0':'20px'}")
                        Col
                            Input(
                                label="  "
                                v-model="form.realty.connect"
                                style="z-index: 1"
                                type="select"
                                :options="realtyConnectItems"
                                optionLabel="label"
                            )
                        Col
                            Input(
                                label="  "
                                v-model="form.realty.consumption"
                                style="z-index: 1"
                                type="select"
                                :options="realtyConsumptionItems"
                                optionLabel="label"
                            )
                    Row(gutter="1.5" unit="rem" style="margin-top:.25rem;align-items:flex-end")
                        Col(style="flex-direction:column")
                            p(style="margin-bottom:.5rem")        ,          ( )  :
                            Group
                                Input(label="" v-model="form.realty.conditionNumber")
                                Input(label="" type="date" v-model="form.realty.conditionDate")
                        Col(style="flex-direction:column")
                            p(style="margin-bottom:.5rem")  ,         (      )*:
                            Input(label=", " v-model="form.realty.date")
        
        div(v-if="step === 2")
            GasCalculator(:items="gasCalculatorItems")

            .form__hr
                .form__hr-text 

            Button(variety="add-list")   

        div(v-if="step === 3")
            p(style="margin: 0 0 1.5rem")      : *.jpg, *.zip, *.pdf
                br
                |       5 
                

            .form__upload-docs
                .form__upload-docs-item(v-for="(item, index) in uploadDocs")
                    .form__upload-docs-name {{ item }}
                    .form__upload-docs-button
                        Button(variety="secondary")  

        div(v-if="step === 4")
            p(style="margin:0")
                strong !&nbsp;
                |     ,  .

        .form__footer
            Button(v-if="step === 4" to="/services/requests")    
            Button(@click.native="step++" v-if="step === 1") 

            .form__footer-buttons(v-if="step === 2 || step === 3")
                Button(@click.native="step--" v-if="step > 1 && step !== 4" variety="white-bordered") 
                Button(@click.native="step++" v-if="step < 4") 

            Checklist(
                :items="['  ,   - ()']",
                type="checkbox"
                v-model="form.eSign"
                itemsColor="font"
                v-if="step === 3"
            )
</template>

<script>
import {
    Row, Col, Input, Checklist, Button, GasCalculator, Group
} from '@/components'

export default {
    name: 'ServicesForm',
    mounted () {
    },
    watch: {
        step (value) {
            document.querySelector('.app__main').scrollTop = 0
            if (this.isMobile) {
                let steps = document.querySelectorAll('.form__wizard-step')
                let wizard = document.querySelector('.form__wizard')
                wizard.scrollLeft = 0
                let offset = 0
                
                for (let i = 1; i <= steps.length; i++) {
                    
                    if (steps[i]) {
                        if (i < value) {
                            let width = steps[i - 1].offsetWidth
                            offset = offset + (width + (6 * i))
                        }
                    }
                }

                if (value !== 4) {
                    wizard.scrollLeft = offset
                } else {
                    wizard.scrollLeft = wizard.offsetWidth
                }
            }
            
        }
    },
    computed: {
        wizardStyle () {
            if (this.isMobile && this.step === 1) {
                return {
                    borderRadius: '4px 0 0 4px',
                    marginRight: '-17px'
                }
            } else if (this.isMobile && (this.step === 2 || this.step === 3)) {
                return {
                    borderRadius: '0',
                    marginRight: '-17px',
                    marginLeft: '-17px'
                }
            } else if (this.isMobile && this.step === 4) {
                return {
                    borderRadius: '0 4px 4px 0',
                    marginLeft: '-17px'
                }
            }
            return {}
        },
        isMobile () {
            return screen.width < 760
        }
    },
    data () {
        return {
            step: 1,
            form: {
                user: {
                    lastName: '',
                    firstName: '',
                    middleName: '',
                    birthDay: '',
                    birthPlace: '',
                    residence: '',
                    inn: '',
                    email: '',
                    phone: '',
                    additionalPhone: ''
                },
                passport: {
                    series: '',
                    number: '',
                    organization: '',
                    issueDate: '',
                    registerPlace: '',
                },
                realty: {
                    type: '',
                    address: '',
                    connect: '',
                    consumption: '',
                    conditionNumber: '',
                    conditionDate: '',
                    date: ''
                },
                eSign: false
            },
            realtyTypeItems: [
                ' ', '', '', ''
            ],
            realtyConnectItems: [
                {
                    label: '  ',
                    value: 1,
                },
                {
                    label: '  ',
                    value: 2,
                },
                {
                    label: '  ',
                    value: 3,
                }
            ],
            realtyConsumptionItems: [
                {
                    label: '  ',
                    value: 1,
                },
                {
                    label: '  ',
                    value: 2,
                },
                {
                    label: '  ',
                    value: 3,
                }
            ],
            gasCalculatorItems: [
                {
                    name: ' ',
                    params: [
                        { name: '', steps: 4 },
                        { name: ' ', steps: 2 }
                    ]
                },
                {
                    name: ' ',
                    params: [
                        { name: '', steps: 4 },
                        { name: ' ', steps: 2 }
                    ]
                },
                {
                    name: ' ',
                    params: [
                        { name: '', steps: 4 },
                        { name: ' ', steps: 2 }
                    ]
                },
                {
                    name: ' ',
                    params: [
                        { name: '', steps: 4 },
                        { name: ' ', steps: 2 }
                    ]
                }
            ],
            uploadDocs: [
                '      (,  ,    ..)',
                '     1  500 (       ',
                ' ,             (    )',
                '           ( 4)',
                '   ,     (          )',
                '         ,       ',
                '  (        )',
                '               ',
                '             '
            ]
        }
    },
    components: {
        Row, Col, Input, Checklist, Button, GasCalculator, Group
    }
}
</script>

<style lang="sass">
@import @/assets/styles/form

.services-form
</style>