<template lang="pug">
div(style="height:100%")
    router-view(v-if="!isEmpty")
    
    
    .layout(v-if="isEmpty")
        .layout__main
            .bills-list
                Card(
                    title="    ?"
                    type="connect-bill"
                    )
                    div
                        |       ( 9 )    
                        span  
                        |      .

                    template(#footer)
                        Button(
                            variety="white"
                            :style="{width: isMobile ? '100%' : 'auto'}"
                        )   
                Card(title="  ?" variety="secondary" type="connect-gas")
                    div           ,       ( ).
                    .card__meta(v-if="isMobile")
                        | :
                        span  90 
                    .card__meta(v-if="isMobile")
                        | :
                        span  65 000 .
                    template(#footer)
                        Button(:style="{width: isMobile ? '100%' : 'auto'}")  
                        .card__meta(v-if="!isMobile")
                            | :
                            span  90 
                        .card__meta(v-if="!isMobile")
                            | :
                            span  65 000 .
</template>

<script>
import {
    Card,
    Button
} from '@/components'

export default {
    name: 'Bills',
    data () {
        return {
            isEmpty: false
        }
    },
    computed: {
        isMobile () {
            return screen.width < 760
        }
    },
    components: {
        Card, Button
    },
}
</script>

<style lang="sass">
@import ~@/assets/styles/vars

.layout
    &__main
        .card
            margin-bottom: 1rem

            @media screen and (max-width: $mobile-width)
                margin-bottom: 8px

            &__meta
                @media screen and (max-width: $mobile-width)
                    margin-left: 0
                    margin-top: 4px
</style>
