<script>
export default {
    name: 'Icon',
    props: {
        color: String,
        icon: String,
    },
    computed: {
        classObject () {
            return {
                'icon__primary': this.color === 'primary',
                'icon__white': this.color === 'white',
                'icon__secondary': this.color === 'secondary',
                'icon__font-mute': this.color === 'font-mute',
                'icon__font-mute-variant': this.color === 'font-mute-variant',
            }
        }
    }
}
</script>

<style lang="sass">
@import @/assets/styles/vars

.icon
    display: flex
    align-items: center
    justify-content: center

    &__primary
        fill: $color-primary

    &__white
        fill: #fff

    &__secondary
        fill: $color-secondary

    &__font-mute
        fill: $color-font-mute

    &__font-mute-variant
        fill: $color-font-mute-variant

    path
        transition: .2s
</style>