<template lang="pug">

.agreements-address-pay
    //- span test
    
    .history__table(v-if="!isMobile")
        .history__table-row.history__table-row_heading
            .history__table-cell(style="width: 60%")  
            .history__table-cell(style="width: 60%") 
            .history__table-cell(style="width: 60%") 
            .history__table-cell(style="width: 60%")  
            .history__table-cell 
        .history__table-row(v-for="bill in getBills" :key="bill.id")
            .history__table-cell(style="width: 60%") {{ bill.number }}
            .history__table-cell(style="width: 60%") {{ formatDate(bill.date) }}
            .history__table-cell(style="width: 60%") {{ formatPrice(bill['summ']) }}
            .history__table-cell(style="width: 60%")
                input(v-if="bill['summ'] !== 0" type="number" v-model="bill['toPay']" placeholder=" ")
                span(v-if="bill['summ'] === 0") {{ formatPrice(0) }}
            .history__table-cell
                div(style="display:flex;align-items:center;height: 100%;")
                    .bills-pay__pay
                        Button(size="small") 
                    .bills-pay__print
                        Button(size="small" variety="secondary")  

    .mobile-table.mobile-table_variant(v-if="isMobile")
        .mobile-table__item
            .mobile-table__title  
            .mobile-table__field
                .mobile-table__key 
                .mobile-table__value
                    span {{ formatPrice(gas.debt) }}
            .mobile-table__field
                .mobile-table__key 
                .mobile-table__value {{ formatPrice(gas.penalties) }}
            .mobile-table__field
                .mobile-table__key 
                .mobile-table__value
                    input(v-if="gas.debt !== 0 || gas.penalties !== 0" type="number" v-model="gas.toPay" placeholder=" ")
                    span(v-if="gas.debt === 0 && gas.penalties === 0") {{ formatPrice(0) }}

    //- .bills-pay__footer.bills-pay__footer_variant
        .bills-pay__total   
            span {{ formatPrice(calculateTotal()) }}
        div(style="display:flex;align-items:center")
            .bills-pay__pay
                Button(size="small") 
            .bills-pay__print
                Button(size="small" variety="secondary")  

    
</template>
//pre {{ getBills }}
<script>
import {Button} from '@/components'
import moment from "moment";

export default {
    name: 'Agreements',
    components: {
        Button
    },
    data () {
        return {
            gas: {
                label: ' ',
                debt: 760,
                penalties: 0,
                toPay: ''
            },
            maintenance: {
                label: '',
                debt: 7296,
                penalties: 0,
                toPay: ''
            },
            stadeDuty: {
                label: '',
                debt: 0,
                penalties: 0,
                toPay: ''
            },
            fines: {
                label: '',
                debt: 0,
                penalties: 0,
                toPay: ''
            },
            connect: {
                label: '',
                debt: 0,
                penalties: 0,
                toPay: ''
            }
        }
    },
    methods: {
        formatPrice (value) {
            return value.toLocaleString('ru-RU') + ' .'
        },
        formatDate (date) {
            return moment(date).format('DD.MM.YYYY')
        },
        calculateTotal () {
            return (this.gas.toPay === '' ? 0 : +this.gas.toPay) + 
                (this.maintenance.toPay === '' ? 0 : +this.maintenance.toPay) + 
                (this.stadeDuty.toPay === '' ? 0 : +this.stadeDuty.toPay) + 
                (this.fines.toPay === '' ? 0 : +this.fines.toPay) + 
                (this.connect.toPay === '' ? 0 : +this.connect.toPay)
        }
    },
    computed: {
        isMobile () {
            return screen.width < 760;
        },
        getBills () {
            return this.$store.getters.getBills/* .find(bill => bill.id === this.$route.params.id); */
        }
    },
    mounted(){
        this.$store.dispatch('getBills', this.$route.params.id);
    }
}
</script>

<style lang="sass">
@import @/assets/styles/vars

.agreements-address-pay

.history__table-cell
    .bills-pay__pay
        span
            color: #fff
</style>
