<template lang="pug">
.agreements-list.layout
    .layout__main
        loading(getter="is_loading")
        .bills-list(v-if="error === ''")
            Card(
                v-for="(agreement, agreementIndex) in getBills"
                angle
                :title="agreement.number ? ` ${agreement.number}` : agreement.name"
                :index="agreementIndex"
                menu
                :key="agreementIndex"
                )
                template(v-slot:card)
                    .bills-list__card-menu(v-if="openedMenu.includes(agreementIndex)")
                        router-link(to="#").bills-list__card-menu-item 
                        router-link(to="#").bills-list__card-menu-item 
                template(v-slot:client) {{ getBills.name }}
                template(v-slot:bill)  : 
                    span {{ agreement.type }}
                template(v-slot:footer)
                    Button(variety="white" :to="`/agreements/${agreement.id}`") 
                    //- Button(variety="white" :to="`/bills/history/`") 

                    Button(variety="primary-light" :to="`/agreements/${agreement.id}/`")  
                    Button(variety="primary-light" :to="`/agreements/${agreement.id}/`" v-if="!isMobile") 
                    Button(variety="primary-light-outline" v-if="!isMobile") 
            //- Button(variety="add-list")  
        .error(v-else)
            h1 {{ error }}
    LayoutSidebar
        router-link(to="#")  
        SidebarSupport
</template>

<script>
import {
    Card,
    LayoutSidebar,
    SidebarSupport,
    Button,
    Loading
} from '@/components'

export default {
    name: 'AgreementsList',
    data () {
        return {
            data: [
                {
                    name: ' 101016437',
                    client: '  ..',
                    type: '5-1234'
                },
            ],
            openedMenu: [],
            error: ''
        }
    },
    components: { Card, LayoutSidebar, SidebarSupport, Button, Loading },
    computed: {
        isMobile () {
            return screen.width < 760
        },
        getBills () {
            try {
                // console.log('test', this.$store.getters.getAgreements)
                return this.$store.getters.getAgreements;
            } catch {
                this.error = '    '
            }
        },
        is_loading() {
            return this.$store.getters.is_loading
        }
    }
}
</script>

<style lang="sass">
@import @/assets/styles/vars

.agreements-list
</style>
